import { Route } from 'vue-router'

import { Logger } from '../../services/logger'
import { Api } from '../../services/api'
import DocLink from './../explorer/docs-area/DocLink.vue'

interface Data {
  monthData: any
}

export default Vue.extend({
  components: {
    'doc-link': DocLink
  },
  data(): Data {
    return {
      monthData: null
    }
  },
  props: {
    monthKey: String
  },
  mounted() {
    Api.loadChangesJson()
      .then(data => {
        let currentMonthData = data.filter(d => d.monthKey === this.monthKey)[0]
        this.monthData = currentMonthData
      })
      .catch(err => {
        Logger.Error(err)
        throw err
      })
  },
  watch: {
    $route(to: Route): void {
      Api.loadChangesJson()
        .then(data => {
          let currentMonthData = data.filter(
            d => d.monthKey === this.monthKey
          )[0]
          this.monthData = currentMonthData
        })
        .catch(err => {
          Logger.Error(err)
          throw err
        })
    }
  }
})



