
































import Vue from 'vue'
import { Route } from 'vue-router'

import { Api } from '../../services/api'
import { Logger } from '../../services/logger'
import { MonthDiffData } from '../../models/MonthDiffData'

interface Data {
  values: MonthDiffData[]
  activeName: string
}

export default Vue.extend({
  data(): Data {
    return {
      values: [],
      activeName: ''
    }
  },
  props: {
    monthKey: String
  },
  mounted() {
    Api.loadChangesJson()
      .then(data => {
        this.values = data
        if (!this.monthKey) {
          this.activeName = data[0].monthKey
        } else {
          this.activeName = this.monthKey
        }
      })
      .catch(err => {
        Logger.Error(err)
        throw err
      })
  },
  watch: {
    $route(to: Route): void {
      Api.loadChangesJson()
        .then(data => {
          if (!this.monthKey) {
            this.activeName = data[0].monthKey
          } else {
            this.activeName = this.monthKey
          }
        })
        .catch(err => {
          Logger.Error(err)
          throw err
        })
    }
  }
})



