import DocLink from './DocLink.vue'
import { toggleTableMixin } from '../../../mixins'

export default Vue.extend({
  components: {
    'doc-link': DocLink
  },
  mixins: [toggleTableMixin],
  computed: {
    funcs(): any[] {
      return this.entity.functions
    }
  },
  props: {
    entity: {
      type: Object,
      required: true
    },
    title: String
  }
})



